import service from "../../service.js";
import headers from "../../components/header_nav.vue"
export default {
	components: {
		headers
	},
	data() {
		return {
			Htitle: "收劵码",
			Hlicon: "../../static/icon/left_whitch.png",
			merchid:uni.getStorageSync("merchid"),
			imgtype:0,
			qrcode:'',
			money:'',
			status:1,
		}
	},
	methods:{
		submit(){
			var self = this;
			if(self.money == '' || self.money < 1){
				uni.showToast({
					icon: 'none',
					title: '收款金额不能小于1'
				});
				return;
			}
			service.downLoad(self, service.api.service.potcode, {
				'merch_id': self.merchid,
				'money':self.money
			},'GET', function(self, res) {
				res = res.data;
				console.log(res)
				if(res.code == 1){
					self.qrcode = res.info;
					self.status = 0;
				}else{
					uni.showToast({
						icon: 'none',
						title: res.info
					});
				}
				
			});
		},
	}
}
